import React from 'react'
import './index.less'
import {Button, Form, Input} from 'antd';
import {baseUrl} from '../../config';
import axios from 'axios'

class NormalLoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_pyq: '',
        }
    }

    componentDidMount() {
        // 
        axios.get(
            baseUrl + '/api/before_generate_pyq',
            {
                params: {
                    token: localStorage.token,
                }
            })
            .then((Response) => {
                console.log(Response.data.id_pyq);
                localStorage.id_pyq = Response.data.id_pyq;
                this.setState({
                    id_pyq: Response.data.id_pyq,
                })
            })
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                // 
                axios.get(
                    baseUrl + '/api/generate_pyq',
                    {
                        params: {
                            token: localStorage.token, //token
                            id_pyq: this.state.id_pyq,  //
                            user_text: values.user_text, //
                        }
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        setTimeout(() => {
                            document.location = `#/admin/repos/upload/img`
                        }, 500);
                    })
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form">
                <h2></h2>
                <Form.Item>
                    {getFieldDecorator('user_text', {
                        rules: [{required: true, message: ''}],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {/* < Link to='/admin/upload/img'> */}
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        
                    </Button>
                    {/* </Link> */}
                </Form.Item>
            </Form>
        )
    }

}

export const ReposUpload = Form.create({name: 'normal_login'})(NormalLoginForm);
