import React from 'react'
import './index.less'
import {Button, Form, Icon, Input, notification} from 'antd';
import {baseUrl} from '../../config';
import axios from 'axios'

class NormalLoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_product: '',
        }
    }

    componentDidMount() {
        // 
        axios.get(
            baseUrl + '/api/before_generate_products',
            {
                params: {
                    token: localStorage.token,
                }
            })
            .then((Response) => {
                console.log(Response.data.id_product);
                localStorage.id_product = Response.data.id_product;
                this.setState({
                    id_product: Response.data.id_product,
                })
            })
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                let urls = [];
                if (values.tburl !== undefined) {
                    urls.push(
                        {
                            type: 'tburl',
                            content: values.tburl,
                        })
                }
                if (values.taok !== undefined) {
                    urls.push(
                        {
                            type: 'taok',
                            content: values.taok,
                        })
                }
                if (values.jd !== undefined) {
                    urls.push(
                        {
                            type: 'jd',
                            content: values.jd,
                        })
                }
                if (values.pdd !== undefined) {
                    urls.push(
                        {
                            type: 'pdd',
                            content: values.pdd,
                        })
                }
                console.log(urls);
                // 
                axios.get(
                    baseUrl + '/api/generate_products',
                    {
                        params: {
                            token: localStorage.token, //token
                            id_product: this.state.id_product,  //
                            product_name: values.product_name,  //
                            price: values.price, //
                            infs: values.infs, //
                            urls: JSON.stringify(urls), //
                        }
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        notification.open({
                            message: '',
                            description: '...',
                            placement: 'bottomLeft',
                            icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                        });
                        setTimeout(() => {
                            document.location = `#/admin/upload/img`
                        }, 500);
                    })
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form">
                <h2></h2>
                <Form.Item>
                    {getFieldDecorator('product_name', {
                        rules: [{required: true, message: ''}],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('price', {
                        rules: [{required: true, message: ''}],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('infs', {
                        rules: [{required: true, message: ''}],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('tburl', {
                        rules: [],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('taok', {
                        rules: [],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('jd', {
                        rules: [],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('pdd')(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>

                <Form.Item>
                    {/* < Link to='/admin/upload/img'> */}
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        
                    </Button>
                    {/* </Link> */}
                </Form.Item>
            </Form>
        )
    }

}

export const ProductsUpload = Form.create({name: 'normal_login'})(NormalLoginForm);
