import React from 'react'
import './index.less'
import {Button, Form, Input} from 'antd';
import {baseUrl} from '../../config';
import axios from 'axios'

class NormalLoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_pyq: '',
        }
    }

    componentDidMount() {

    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                // 
                axios.post(
                    baseUrl + '/api/update_seller_data',
                    {
                        token: localStorage.token, //token
                        type: 'sellerName',
                        data: values.seller_name, //
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        setTimeout(() => {
                            document.location = `#/admin/seller/upload/img`
                        }, 500);
                    })
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form">
                <h2></h2>
                <Form.Item>
                    {getFieldDecorator('seller_name', {
                        rules: [{required: true, message: ''}],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {/* < Link to='/admin/upload/img'> */}
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        
                    </Button>
                    {/* </Link> */}
                </Form.Item>
            </Form>
        )
    }

}

export const SellerUpload = Form.create({name: 'normal_login'})(NormalLoginForm);
